.catalog__content {
  display: flex;
}

.catalog__breadcrumbs {
  margin-bottom: 20px;
  &-list {
    padding: 5px 0;
    a {
      text-decoration: underline;
    }
    li {
      display: inline-block;
      position: relative;
      & + li {
        padding-left: 15px;
        &::before {
          content: "/";
          position: absolute;
          left: 2px;
        }
      }
      &:last-child a {
        text-decoration: none;
      }
    }
  }
}




.catalog__categories {
  width: 200px;
  margin-bottom: 100px;
  @include font_size-lh-fw(20px, 20px, 400);
}

.catalog__category {
  margin-bottom: 20px;
  & a:hover {
    text-decoration: underline;
  }
  &_active {
    a {
      text-decoration: underline;
    }
  }
}

.catalog__subcategories {
  margin-top: 5px;
  @include font_size-lh-fw(14px, 22px, 400);
  margin-left: 20px;
  list-style: disc;
  a {
    text-decoration: none;
  }
}

.catalog__subcategory_active {
  a {
    text-decoration: underline;
    color: #000000;
  }
}

.catalog__price-list-link {
  width: 200px;
  display: block;
  text-align: center;
  img {
    display: block;
    margin: 0 auto;
    width: 95px;
    height: 95px;
  }
  span {
    @include font_size-lh-fw(30px, 25px, 700)
  }
  &:hover {
    text-decoration: underline;
  }
}

.catalog__list {
  display: flex;
  max-width: 780px;
  flex-wrap: wrap;
  column-gap: 15px;
  row-gap: 20px;
  margin-bottom: 30px;
}

.catalog__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 250px;
  min-height: 380px;
  padding: 20px;
  background-color: $bg-color;
  &-image{
    width: 210px;
    height: 210px;
  }

  &-description {
    @include font_size-lh-fw(18px, 18px, 400);
    letter-spacing: 0.05em;
    padding-bottom: 30px;
  }

  &-summary {
    display: flex;
    justify-content: space-between;
  }
  &-cart {
    padding-right: 16px;
  }
  &-price {
    @include font_size-lh-fw(28px, 18px, 700);
  }
}

.catalog__pager {
  margin-bottom: 65px;
  &-list {
    display: flex;
    justify-content: center;
    column-gap: 20px;
  }
  &-item {
    display: inline-block;
    &_active {
      color: #444444;
      font-size: 20px;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}

.catalog__about-us {
  display: flex;
  justify-content: space-between;
  column-gap: 40px;
  margin: 30px 0 205px;
  &-column {
    flex: 1;
    max-width: 370px;
  }
}